<template>
  <div class="quill-editor">
    <section>
			<h1 class="h1">vue-quill-editor </h1>
			<quill-editor
				id="editor"
				v-model="content"
				ref="myQuillEditor"
				:options="editorOption"
				@blur="onEditorBlur($event)"
				@focus="onEditorFocus($event)"
				@change="onEditorChange($event)"
				>
			</quill-editor>
	</section>
	<div class="box">
		<span class="num">{{ TiLength }} / 800</span>
		<!-- <el-button type="primary" @click="getContent"></el-button> -->
		<p class="mt20 mb10"><span v-text="contents"></span></p>
		<p class="mb10">https://quilljs.com/docs/api/#content</p>
		<p>: https://www.kancloud.cn/liuwave/quill/1409378</p>
	</div>
  </div>
</template>

<script>
import { quillEditor } from "vue-quill-editor" // 
import "quill/dist/quill.core.css"
import "quill/dist/quill.snow.css"
import "quill/dist/quill.bubble.css"

export default {
  data () {
    return {
      toolbarTips: [
				{Choice:'.ql-bold',title:''},
				{Choice:'.ql-italic',title:''},
				{Choice:'.ql-underline',title:''},
				{Choice:'.ql-header',title:''},
				{Choice:'.ql-strike',title:''},
				{Choice:'.ql-blockquote',title:''},
				{Choice:'.ql-code-block',title:''},
				{Choice:'.ql-size',title:''},
				{Choice:'.ql-list[value="ordered"]',title:''},
				{Choice:'.ql-list[value="bullet"]',title:''},
				{Choice:'.ql-header[value="1"]',title:'h1'},
				{Choice:'.ql-header[value="2"]',title:'h2'},
				{Choice:'.ql-align',title:''},
				{Choice:'.ql-color',title:''},
				{Choice:'.ql-background',title:''},
				{Choice:'.ql-image',title:''},
				{Choice:'.ql-video',title:''},
				{Choice:'.ql-link',title:''},
				{Choice:'.ql-formula',title:''},
				{Choice:'.ql-clean',title:''},
				{Choice:'.ql-indent[value="-1"]',title:''},
				{Choice:'.ql-indent[value="+1"]',title:''},
				{Choice:'.ql-header .ql-picker-label',title:''},
				{Choice:'.ql-header .ql-picker-item[data-value="1"]',title:''},
				{Choice:'.ql-header .ql-picker-item[data-value="2"]',title:''},
				{Choice:'.ql-header .ql-picker-item[data-value="3"]',title:''},
				{Choice:'.ql-header .ql-picker-item[data-value="4"]',title:''},
				{Choice:'.ql-header .ql-picker-item[data-value="5"]',title:''},
				{Choice:'.ql-header .ql-picker-item[data-value="6"]',title:''},
				{Choice:'.ql-header .ql-picker-item:last-child',title:''},
				{Choice:'.ql-size .ql-picker-item[data-value="small"]',title:''},
				{Choice:'.ql-size .ql-picker-item[data-value="large"]',title:''},
				{Choice:'.ql-size .ql-picker-item[data-value="huge"]',title:''},
				{Choice:'.ql-size .ql-picker-item:nth-child(2)',title:''},
				{Choice:'.ql-align .ql-picker-item:first-child',title:''},
				{Choice:'.ql-align .ql-picker-item[data-value="center"]',title:''},
				{Choice:'.ql-align .ql-picker-item[data-value="right"]',title:''},
				{Choice:'.ql-align .ql-picker-item[data-value="justify"]',title:''}
      ],
      content: "",
      contents: "",
      TiLength: 0,
      editorOption: {
        placeholder: "...",
        modules: {
           toolbar:[
            ['bold', 'italic', 'underline', 'strike'],    //
            ['blockquote', 'code-block'],     //
      
            [{ 'header': 1 }, { 'header': 2 }],        // 12
            [{ 'list': 'ordered'}, { 'list': 'bullet' }],     //
            [{ 'indent': '-1'}, { 'indent': '+1' }],     // 
      
            [{ 'size': ['small', false, 'large', 'huge'] }], // 
            [{ 'header': [1, 2, 3, 4, 5, 6, false] }],     // 
      
            [{ 'color': [] }, { 'background': [] }],     // 
            [{ 'align': [] }],    //
      
            ['clean'],    //
            ['image']    //
           ] //
         }
      },
      theme:'snow'
    }
  },
  components: {
    quillEditor
  },
  mounted () {
    // this.editor.container.style.height = `${500}px`
    for(let item of this.toolbarTips){
			let tip = document.querySelector('.quill-editor '+ item.Choice)
			if (!tip) continue
			tip.setAttribute('title',item.title)
    }
  },
  computed: {
    editor() {
      // false  true
      this.$refs.myQuillEditor.quill.enable(false)
      // return this.$refs.myQuillEditor.quill;
    }
  },
  methods: {
    getContent () {
      // var quill = new Quill('#editor');
      // var txt = quill.getContents();
      // var length = quill.getLength();
      // console.log(txt);
      // console.log(length);
      // console.log(this.content);
    },
    onEditorReady(editor) { }, // 
    
    // 
    onEditorBlur(val, editor) {
      // console.log('222');
      // console.log(val);
      // console.log(val.container.innerHTML);
      // console.log(val.container.innerText);
      // console.log(val.container.outerHTML);
      // console.log(val.container.outerText);
    }, 
    // 
    onEditorFocus(val, editor) {
      // console.log('111');
      // console.log(val); // 
    },
    //  
    onEditorChange(event) {
      event.quill.deleteText(800,4)
      if(this.content === '') {
        this.TiLength = 0
      } else {
        this.TiLength = event.quill.getLength()-1
      }
      // if (this.TiLength > 10) {
      //   this.$message.error('10')
      //   // 
      //   this.$refs.myQuillEditor.quill.enable(false)
      // }
      // console.log('wxyjolin');
      // console.log(event.html);
      // console.log(event.text);
      this.contents = event.text
    }
  }
}
</script>
<style lang="less" scope>
.quill-editor {
	position: relative;
	left: 110px;
	z-index: 9;
	width: 900px;
	.ql-toolbar, .ql-container {
		background-color: #fff;
	}
	.h1, .box{
		margin-left: 110px;
		margin-bottom: 10px;
		text-align: left;
	}
	.box {
		.num {
			display: inline-block;
			margin-top: 10px;
		}
	}
}
</style>
<style lang="less">
.quill-editor {
  .ql-container {
		height: 500px;
			text-align: left;
  }
}
</style>